export const lengthControl = (Maxlen) => {
    return (text) => {
        let result = undefined
        if (!text) result = '   ';
        if (result === undefined) {
            if (text.length > Maxlen) result = '   ,  ' + Maxlen;
        }
        return result;
    }
}

export const StageControl = (text) => {
        let result = undefined
        try {
            if (Number(text) > 6 || Number(text) < 0) result = '   (1-6)';
        }
        catch {
            result = '   (1-6)';
        }
        return result;
}