import React, { useState } from 'react'
import s from './Game.module.css'

function Game(props) {


    let TempScore = [];
    for (let i = 0; i < props.PlayersCount; i++) {
        TempScore.push(0);
    }

    let TempBonus = [];
    for (let i = 0; i < props.PlayersCount; i++) {
        TempBonus.push(false);
    }
    let [PlayersBonus, SetPlayersBonus] = useState(TempBonus);
    
    let [PlayersScore, SetPlayersScore] = useState(TempScore);
    console.log(PlayersScore)

    let [PlayerRes, SetPlayerRes] = useState([0, 0, 0, 0, 0]);
    let [Raund, SetRaund] = useState(1);
    let [Player, SetPlayer] = useState(1);
    let [isNext, SetisNext] = useState(true);
    let [isEnd, SetisEnd] = useState(false);

    let FirstCheck = (Res) => {

        let Count1 = 0;
        let Count2 = 0;
        let check1 = 0;
      
        let Count = 0;
        let Temp = -1;
      
        for (let i = 0; i < 6; i++) {
           if ((i != 6) && (Res[i] == Temp)) Count++
           else {
             if ((Count == 2) && (Count1 == 0)) Count1 = Count
             else if ((Count == 2) && (Count1 != 0)) Count2 = Count
             else if ((Count == 3) && (Count1 == 0)) Count1 = Count
             else if ((Count == 3) && (Count1 != 0)) Count2 = Count
             else if (Count == 4) Count1 = Count
             else if (Count == 5) Count1 = Count;
      
             if (i != 6) Temp = Res[i];
             Count = 1;
            }
        }
      
        if (Count1 == 5) check1 = 7
        else if (Count1 == 4) check1 = 6
        else if ((Count1 == 3) &&(Count2 == 2)) check1 = 5
        else if (Count1 == 3) check1 = 4
        else if ((Count1 == 2) && (Count2 == 2)) check1 = 3
        else if (Count1 == 2) check1 = 2
        else check1 = 1;

        return check1;
    }



    let NewScore = (TempRes) => {
        let i;
        let temp;

        let Res = [...TempRes];

        for (let i = 0; i < 5; i++) {
            for (let j = 0; j < 5 - i; j++) {
                if (Res[j] > Res[j + 1]) {
                    temp = Res[j];
                    Res[j] = Res[j + 1];
                    Res[j + 1] = temp;
                }
            }
        }

        let ResuleOfFirstCheck = FirstCheck(Res);

        let Score = 0;
        let Count = 0;
        let Temp = -1;
        let Sum = 0;
     
        for (i = 0; i < 5; i++) {
          if (Res[i] === Temp) Count++;
          else {
            if (Count >= 2) {
                Score = Score + Count
                Sum = Sum + Res[i - 1] * Count
            }
            Temp = Res[i];
            Count = 1;
        }
        }
        if (Count >= 2) {
            Score = Score + Count
            Sum = Sum + Res[i - 1] * Count
        }

        if (Count == 5) {
            let newState = [...PlayersBonus]
            if (!newState[Player - 1]) {
                NewScore = 100;
            }
            newState[Player - 1] = true;
            SetPlayersBonus(newState)
        }
        console.log(PlayersBonus)

        let NewScore = 0;
        console.log(Res)
        console.log(Score)
        console.log('Sum')
        console.log(Sum)
        if (Score != 0) NewScore = ResuleOfFirstCheck * 50 + Math.round((Sum/Score) * 10);
        else NewScore = ResuleOfFirstCheck * 50 


        return NewScore
    }

    let [RaundScore, SetRaundScore] = useState(1);

    let PlayerClick = () => {
        let i;
        let TempRes = [0, 0, 0, 0, 0]
        for (i = 0; i < 5; i++) {
            TempRes[i] = Math.floor(Math.random() * (2)) + 1;
            SetPlayerRes(TempRes);
        }
        SetRaundScore(NewScore(TempRes))
        let newState = [...PlayersScore]
        newState[Player - 1] = newState[Player - 1] + NewScore(TempRes);
        SetPlayersScore(newState)
        SetisNext(false);
    } 

    let Next = () => {
        if (isEnd) {
            props.SetisStart(false)
        }
        SetPlayer(Player + 1)
        if (Player >= props.PlayersCount) {
            SetPlayer(1);
            SetRaund(Raund + 1);
            if (Raund + 1 > 10) {
                SetisEnd(true)
            }
        }
        if (!isEnd) SetisNext(true);
    }

    let Winner = () => {
        //PlayersScore  
        let max = 0;
        let WinnersS = '';
        for (let i = 0; i < props.PlayersCount; i++) {
            if (PlayersScore[i] > PlayersScore[max]) {
                WinnersS = ''
                WinnersS = String(max + 1)
                max = i
            }
            else if (PlayersScore[i] == PlayersScore[max]) {
                if (max == 0) {
                    WinnersS = String(max + 1)
                }
                else WinnersS = WinnersS + ', ' + String(max + 1)
            }
        }
        return WinnersS
    }

    return (
        <div className={s.WinnersBlock}>
            { !isEnd ? <div> : {Raund}</div> : <></> }
            {!isNext && !isEnd ? 
                <div>
                    <div> {Player}:</div>
                    <div> : {PlayerRes[0]}, {PlayerRes[1]}, {PlayerRes[2]}, {PlayerRes[3]}, {PlayerRes[4]}</div>
                    <div> : {RaundScore}</div>
                    <div> : {PlayersScore[Player-1]}</div>
                </div> : !isNext && isEnd ? <div>: {Winner()}</div> : <></>
                }
            {isNext ?
                <button onClick={PlayerClick}> {Player}, </button>
            :   <button onClick={Next}></button>  }
        </div>
    );
}


export default Game;