import React, { useState } from 'react'
import s from './Content.module.css'
import Game from './Game/Game';
import Data from '../../../../BLL/Winners.json'
import { useFormik } from 'formik';

function Content(props) {

    let [isStart, SetisStart] = useState(false);

    const validate = values => {
        const errors = {};
    
        if (values.PlayersCount < 2) { errors.PlayersCount = " 2 ." }
    
        return errors;
    };



    const formik = useFormik({
        initialValues: {
            PlayersCount: null,
        },
        validate,
        onSubmit: (values) => {
            SetisStart(true)
        },
    });



    return (
        <div className={s.Content}>
            
            <form className={s.Content} onSubmit={formik.handleSubmit}>
                { !isStart ? <>
                    
                <span>  : </span>
                <input className={formik.errors.PlayersCount && formik.touched.PlayersCount ? s.inputErr : s.input} 
                    id='PlayersCount' type="number" onChange={formik.handleChange} value={formik.values.PlayersCount}></input>
                {formik.errors.PlayersCount && formik.touched.PlayersCount ? <div className={s.errorMessage}>{formik.errors.PlayersCount}</div> : null}
            <button type="submit"></button></>
                : <>
                    <Game SetisStart={SetisStart} PlayersCount={formik.values.PlayersCount}/>
                </>}
            </form>
            <div className={s.Footer}>
                <div> :</div>
                <div> ,  </div>
                <div> :</div>
                <div> ,  </div>
                <div> :</div>
                <div> ,  </div>
                <div>:  </div>
            </div>
        </div>
    );
}


export default Content;